/**
 * 
 * 
 * @param {any} Arr 
 * @param {any} content 
 * @returns 
 */
function deleteContentFromArr(Arr, content){
    var len = Arr.length;
    for(var i=0; i<len; i++) {
        if (Arr[i] == content) {
            Arr.splice(i, 1);
            return;//contentreturn
        }
    }
}
/**
 * 
 * 
 * @param {any} ele 
 * @param {any} css 
 * @param {any} cb 
 */
$.button = function(ele,css,cb){
    var startX;
    var startY;
    var isCancel = false;
    var $ele = $(ele);
    var hasTouch = ('ontouchstart' in window);
    if (hasTouch) {
        $ele.off('touchstart').on('touchstart',function(e){
            // console.log(e);
            var touch = e.originalEvent.touches[0];
            startX = touch.pageX;
            startY = touch.pageY;
            $(this).addClass(css);
            isCancel = false;
        })
        $ele.off('touchmove').on('touchmove',function(e){
            var touch = e.originalEvent.touches[0];
            if (Math.abs(touch.pageX - startX) > 10 || Math.abs(touch.pageY - startY) > 10) {
               $(this).removeClass(css);
               isCancel = true;
            }
        })
        $ele.off('touchend').on('touchend',function(e){
            $(this).removeClass(css);
            if (!isCancel) {
               if (cb) {
                   cb.apply(this)
               }
            }
        })
    } else {
        $ele.off('mousedown').on('mousedown',function(e){
            startX = e.pageX;
            startY = e.pageY;
            $(this).addClass(css);
            isCancel = false;
        })
        $ele.off('mouseup').on('mouseup',function(e){
            $(this).removeClass(css);
            if (Math.abs(e.pageX - startX) > 10 || Math.abs(e.pageY - startY) > 10) {
               isCancel = true;
            }
           if (!isCancel) {
               if (cb) {
                   // cb.apply(this)
                   cb.bind(this)()
               }
           }
        })
    }
}


